import React, { Component } from 'react'
import hoverEffect from 'hover-effect';
import img1 from "../media/index-img-two.jpg"
import img2 from "../media/index-img-one.jpg"
import img3 from "../media/fluid.jpg"
import map from "../media/heightMap.png"

export default class Slideshow extends Component {
    constructor(props) {
        super(props);
        this.state = {
          animation: undefined,
          isLast: false
        }
        this.canvasRef = React.createRef();
    }

    componentDidMount() {
      this.slideshow(this.state.currentImage)
      const interval = setInterval(() => {
        if (this.state.isLast) {
          this.state.animation.previous()

        } else {
          this.state.animation.next()
        }
        this.setState({ isLast: !this.state.isLast })
      }, 5000)

    }

    slideshow = (num) => {
      const animation = new hoverEffect({
        parent: this.canvasRef.current,
        intensity: 0.2,
        image1: img1,
        image2: img2,
        displacementImage: map,
        hover: false
      });
      this.setState({ animation })
    }

    render() {
      return (
        <div ref={this.canvasRef} className="canvas" style={{ width: '100%' }}></div>
      );
    }
}
