import React, { Component } from 'react'
import Overlay from './Overlay'

export default class ShopItem extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isOpen: false
        }
    }

    handleOverlayOpen = () => {
        this.setState({ isOpen: true })
    }

    handleOverlayClose = () => {
        this.setState({ isOpen: false })
    }

    render() {
        const { img, title, content } = this.props
        return (
            <div>
                <div onClick={this.handleOverlayOpen} className="shop-item">
                    <img src={img} className="standard-img" alt=""/>
                    <p className="anno-standard">(Example 2020)</p>
                </div>
                {
                    this.state.isOpen ? (
                        <Overlay onClose={this.handleOverlayClose}>
                            <img src={img} className="overlay-img" alt=""/>
                            <div className="overlay-content">
                                <h3>{title}</h3>
                                <p className="anno-standard">{content}</p>
                            </div>
                        </Overlay>
                    ) : null
                }
            </div>
        )
    }
}
